@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Formular";
  font-style: normal;
  src: local("Formular Bold"), local("Formular-Bold"), url("../fonts/formular-bold.woff2") format("woff2"), url("../fonts/formular-bold.woff") format("woff");
}
/* ------------------------------------ /
  #CSS custom properties
/* ------------------------------------ */
:root {
  --color-bg-white: #ffffff;
  --color-bg-grey-on-white: #f1f1f1;
  --color-bg-element: #e6e6e6;
  --color-bg-shadow: rgb(34 34 34 / 80%);
  --color-text-basic: #222222;
  --color-text-descriptor: #767676;
  --color-icon: #a7a7a7;
  --color-outline: #d8d8d8;
  --color-action-selected: #abc8ea;
  --color-action-blue: #0656b4;
  --fz-basic: 14px;
  --lh-basic: 22px;
  --fz-descriptor: 12px;
  --lh-descriptor: 22px;
  --shadow: 0 7px 38px rgb(44 62 106 / 25%);
}

/* ------------------------------------ /
  #SCSS-variables
/* ------------------------------------ */
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

.page {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  min-height: 100%;
  margin: 0;
  color: var(--color-text-basic);
  font-weight: 400;
  font-family: "Open Sans", sans-serif;
  font-size: var(--fz-basic);
  line-height: var(--lh-basic);
  background-color: var(--color-bg-grey-on-white);
}

img {
  max-width: 100%; /* чтобы картинка не стала больше чем её контейнер */
  height: auto; /* картинка сохраняет пропорции */
  vertical-align: top;
}

button {
  padding: 0;
  border: none; /* в норме есть обводка */
  font: inherit; /* в норме monospace */
  background-color: transparent;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  max-width: 1216px;
  margin: 0 auto;
  padding: 0 8px;
}

[data-simplebar] {
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
}

.simplebar-wrapper {
  width: inherit;
  max-width: inherit;
  height: inherit;
  max-height: inherit;
  overflow: hidden;
}

.simplebar-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  width: auto !important;
  height: auto !important;
  margin: 0;
  padding: 0;
  overflow: hidden;
  direction: inherit;
}

.simplebar-offset {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-sizing: inherit !important;
  margin: 0;
  padding: 0;
  direction: inherit !important;
  resize: none !important;
  -webkit-overflow-scrolling: touch;
}

.simplebar-content-wrapper {
  position: relative;
  display: block;
  box-sizing: border-box !important;
  width: auto;
  max-width: 100%; /* Not required for horizontal scroll to trigger */
  height: 100%; /* Required for horizontal native scrollbar to not appear if parent is taller than natural height */
  max-height: 100%; /* Needed for vertical scroll to trigger */
  direction: inherit;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
}

.simplebar-content:before,
.simplebar-content:after {
  display: table;
  content: " ";
}

.simplebar-placeholder {
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  pointer-events: none;
}

.simplebar-height-auto-observer-wrapper {
  position: relative;
  z-index: -1;
  flex-grow: inherit;
  flex-shrink: 0;
  flex-basis: 0;
  float: left;
  box-sizing: inherit !important;
  width: 100%;
  max-width: 1px;
  height: 100%;
  max-height: 1px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  pointer-events: none;
}

.simplebar-height-auto-observer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  box-sizing: inherit;
  width: 1000%;
  min-width: 1px;
  height: 1000%;
  min-height: 1px;
  overflow: hidden;
  opacity: 0;
  pointer-events: none;
}

.simplebar-track {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  overflow: hidden;
}

[data-simplebar].simplebar-dragging .simplebar-content {
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
}

[data-simplebar].simplebar-dragging .simplebar-track {
  pointer-events: all;
}

.simplebar-scrollbar {
  position: absolute;
  right: 2px;
  left: 0;
  width: 11px !important;
  height: 50px !important;
  min-height: 10px;
  cursor: pointer;
}

.simplebar-scrollbar:before {
  position: absolute;
  right: 2px;
  left: 2px;
  z-index: 1;
  border-radius: 7px;
  background: var(--color-outline);
  opacity: 0;
  transition: opacity 0.2s linear, 200ms background-color;
  content: "";
}

.simplebar-scrollbar.simplebar-visible:before {
  /* When hovered, remove all transitions from drag handle */
  opacity: 0.5;
  transition: opacity 0s linear;
}

.simplebar-track.simplebar-vertical {
  top: 0;
  width: 11px;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
  top: 2px;
  bottom: 2px;
}

.simplebar-track.simplebar-horizontal {
  left: 0;
  height: 11px;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar:before {
  right: 2px;
  left: 2px;
  height: 100%;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
  top: 2px;
  right: auto;
  left: 0;
  width: auto;
  min-width: 10px;
  height: 7px;
  min-height: 0;
}

/* Rtl support */
[data-simplebar-direction=rtl] .simplebar-track.simplebar-vertical {
  right: auto;
  left: 0;
}

.hs-dummy-scrollbar-size {
  position: fixed;
  width: 500px;
  height: 500px;
  overflow-x: scroll;
  overflow-y: hidden;
  direction: rtl;
  visibility: hidden;
  opacity: 0;
}

.simplebar-hide-scrollbar {
  position: fixed;
  left: 0;
  overflow-y: scroll;
  visibility: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.input {
  padding: 14px 25px 12px;
  border: 2px solid var(--color-outline);
  border-radius: 100px;
  font-size: var(--fz-basic);
  line-height: var(--lh-basic);
  outline: none;
}
.input:focus {
  border-color: var(--color-action-selected);
}

.modal {
  border-radius: 10px;
  background-color: var(--color-bg-white);
  box-shadow: var(--shadow);
}

.header {
  padding: 16px;
  background-color: var(--color-bg-white);
}
@media (min-width: 1000px) {
  .header {
    padding-bottom: 0;
  }
}

.header__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  align-items: center;
  grid-row-gap: 8px;
}
@media (min-width: 1000px) {
  .header__container {
    grid-template-columns: auto auto 1fr auto auto;
    justify-content: start;
  }
}

.header__logo {
  justify-self: center;
}
@media (min-width: 1000px) {
  .header__logo {
    justify-self: start;
    margin-right: 16px;
  }
}

.header__favourites {
  justify-self: end;
  margin-right: 3px;
}
@media (min-width: 1150px) {
  .header__favourites {
    margin-right: 30px;
  }
}

.header__region-menu {
  margin-right: 16px;
}

.header__search {
  display: none;
  width: 100%;
  max-width: 592px;
  margin-right: 20px;
  border-color: var(--color-bg-grey-on-white);
  background-color: var(--color-bg-grey-on-white);
  transition: 200ms background-color;
}
.header__search::placeholder {
  text-align: center;
}
.header__search:focus {
  background-color: transparent;
}
@media (min-width: 1000px) {
  .header__search {
    display: block;
  }
}

.header__profile-link.profile-link {
  display: none;
}
@media (min-width: 1000px) {
  .header__profile-link.profile-link {
    display: flex;
  }
}

.header__nav {
  display: none;
  grid-column: 1/-1;
}
@media (min-width: 1000px) {
  .header__nav {
    display: block;
  }
}

@media (min-width: 1000px) {
  .nav-modal {
    display: none;
  }
}

.menu-button {
  position: relative;
  width: 32px;
  height: 32px;
  padding: 8px 6px;
  cursor: pointer;
}

.menu-button__lines {
  display: block;
  height: 2px;
  background-color: black;
  transition: transform 200ms ease 0s;
}
.menu-button__lines::before, .menu-button__lines::after {
  position: absolute;
  left: 6px;
  width: 20px;
  height: 2px;
  background-color: black;
  transform-origin: left;
  transition: transform 200ms;
  content: "";
}
.menu-button__lines::before {
  top: 8px;
}
.menu-button__lines::after {
  bottom: 8px;
}

.nav-modal--open .menu-button__lines {
  height: 0;
}
.nav-modal--open .menu-button__lines::before {
  transform: rotate(45deg);
}
.nav-modal--open .menu-button__lines::after {
  transform: rotate(-45deg);
}

.modal-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  visibility: visible;
  transition: 500ms visibility;
}

.modal-menu__shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--color-bg-shadow);
  opacity: 1;
  transition: 500ms opacity;
}

.modal-menu__content {
  position: relative;
  left: 0;
  z-index: 1;
  width: 300px;
  height: 100%;
  padding: 0 16px;
  border-radius: 0 10px 10px 0;
  background-color: var(--color-bg-white);
  transition: 500ms left;
  transition-delay: 200ms;
}

.modal-menu__region {
  margin-bottom: 30px;
}

.nav-modal:not(.nav-modal--open) .nav-modal__modal-menu {
  visibility: hidden;
}

.nav-modal:not(.nav-modal--open) .modal-menu__shadow {
  opacity: 0;
}

.nav-modal:not(.nav-modal--open) .modal-menu__content {
  left: -100%;
  transition-delay: 0ms;
}

.modal-menu__profile-link {
  padding-top: 24px;
  padding-bottom: 16px;
}

.modal-menu__close-button {
  position: absolute;
  top: 10px;
  right: -35px;
  width: 30px;
  height: 30px;
  color: white;
}

.modal-menu__close-icon {
  fill: var(--color-bg-white);
}

.favourites {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 9px;
  border-radius: 37px;
  transition: 300ms background-color;
}

.favourites:hover,
.favourites:focus-within {
  background-color: var(--color-bg-grey-on-white);
}

.logo {
  flex-shrink: 0;
  transition: 200ms opacity;
}
.logo:hover, .logo:focus {
  opacity: 0.8;
}

.profile-link {
  display: flex;
  align-items: center;
  gap: 7px;
  width: fit-content;
  margin: 0 auto;
  transition: 300ms color;
}

.profile-link:hover,
.profile-link:focus-within {
  color: var(--color-text-descriptor);
}

.region {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 8px 16px;
  border: 1px solid var(--color-outline);
  border-radius: 5px;
  text-align: left;
}

.region__text {
  display: flex;
  flex-direction: column;
}

.region__label {
  color: var(--color-text-descriptor);
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
}

.region__icon {
  color: var(--color-icon);
  fill: red;
}

.mobile-nav-box__categories-list {
  display: flex;
  justify-content: space-around;
  margin: 0 -16px;
  margin-bottom: 15px;
  padding: 0;
  border-bottom: 1px solid var(--color-outline);
  list-style: none;
}

.mobile-nav-box__categories-item {
  position: relative;
  padding-bottom: 12px;
  color: var(--color-text-descriptor);
}

.mobile-nav-box__categories-item--active {
  color: var(--color-text-basic);
}
.mobile-nav-box__categories-item--active::before {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: var(--color-action-blue);
  content: "";
}

.mobile-nav-box__categories-button {
  color: inherit;
  font-weight: 700;
  font-family: "Formular", sans-serif;
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: 200ms color;
}
.mobile-nav-box__categories-button:hover, .mobile-nav-box__categories-button:focus {
  color: var(--color-action-blue);
}

.mobile-link-list {
  display: none;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  list-style: none;
}
.mobile-link-list--active {
  display: block;
}

.mobile-link-list__link {
  display: block;
  width: fit-content;
  padding: 10px 8px;
  transition: 200ms color;
}
.mobile-link-list__link:hover {
  color: var(--color-text-descriptor);
}

.secondary-list {
  margin: 0;
  padding: 0;
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
  list-style: none;
}

.secondary-list__item {
  color: var(--color-text-descriptor);
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
}

.secondary-list__item + .secondary-list__item {
  margin-top: 20px;
}

.secondary-list__link {
  padding: 5px 8px;
}

.region-menu {
  position: relative;
  display: none;
  width: 134px;
}
@media (min-width: 1000px) {
  .region-menu {
    display: block;
  }
}

.region-menu__button {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  text-align: left;
}

.region-menu__text {
  display: block;
  overflow: hidden;
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
  white-space: nowrap;
  text-overflow: ellipsis;
}

.region-menu__icon {
  color: var(--color-icon);
}
.region-menu__icon:hover svg {
  fill: var(--color-bg-shadow);
}

.region-menu__modal {
  position: absolute;
  top: 200%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 384px;
  padding-top: 24px;
  visibility: hidden;
  opacity: 0;
}
.region-menu__modal.active {
  top: calc(100% + 20px);
  visibility: visible;
  opacity: 1;
  transition: 200ms;
}

.region-menu__input {
  width: 352px;
}

.region-menu__save-button {
  width: 100%;
  padding: 16px;
  border-radius: 0 0 10px 10px;
  color: var(--color-bg-white);
  font-family: "Formular", sans-serif;
  font-size: var(--fz-descriptor);
  line-height: var(--lh-descriptor);
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: var(--color-action-blue);
  transition: 300ms background-color;
}
.region-menu__save-button:disabled {
  border-top: 1px solid var(--color-outline);
  color: var(--color-text-descriptor);
  background-color: var(--color-bg-white);
  cursor: auto;
}

.region-menu__list {
  width: 100%;
  height: 223px;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  overflow: auto;
  list-style: none;
}

.region-menu__loader {
  display: none;
  padding: 10px;
}
.region-menu__loader.active {
  display: block;
}

.region-menu__item ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.region-menu__item button {
  width: 100%;
  padding: 11px 16px;
  text-align: left;
  transition: 200ms background-color;
}
.region-menu__item button[data-region-type=city] {
  display: flex;
  flex-direction: column;
}
.region-menu__item button:hover, .region-menu__item button:focus {
  background-color: var(--color-bg-grey-on-white);
}
.region-menu__item button.hide {
  display: none;
}
.region-menu__item button b {
  color: var(--color-action-blue);
}

.region-menu__subtitle {
  color: var(--color-text-descriptor);
}

.labels {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin: 0;
  padding: 12px 16px;
  list-style: none;
}

.labels__item {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  gap: 10px;
  padding: 9px 17px;
  border-radius: 50px;
  color: var(--color-bg-white);
  background-color: var(--color-action-blue);
}

.label__button {
  width: 14px;
  height: 14px;
  padding: 3px;
  color: white;
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='current' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 1.41L12.59 0L7 5.59L1.41 0L0 1.41L5.59 7L0 12.59L1.41 14L7 8.41L12.59 14L14 12.59L8.41 7L14 1.41Z' fill='white'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-size: contain;
}

.nav {
  position: relative;
  max-width: 1200px;
  padding: 6px 0;
  overflow-x: clip;
  overflow-y: visible;
}

.nav__list {
  display: flex;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: 300ms;
}

.nav__item {
  position: relative;
}
.nav__item--active .nav__link {
  background-color: var(--color-bg-grey-on-white);
}

.nav__link {
  display: block;
  width: max-content;
  padding: 10px;
  border-radius: 34px;
  transition: 200ms opacity;
}
.nav__link:hover, .nav__link:focus {
  opacity: 0.8;
}

.nav__controls {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.nav__button {
  width: 50px;
  height: 40px;
  background-color: var(--color-bg-white);
  background-image: url("../../img/icons/arrow-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  pointer-events: auto;
}
.nav__button--left {
  box-shadow: -20px 0 15px var(--color-bg-white);
  transform: rotate(180deg);
}
.nav__button--right {
  box-shadow: -20px 0 15px var(--color-bg-white);
}

.nav--right-shift .nav__button--right {
  visibility: hidden;
}

.nav:not(.nav--right-shift) .nav__button--left {
  visibility: hidden;
}

.nav__link:hover ~ .nav__modal-list,
.nav__modal-list:hover {
  visibility: visible;
}

.nav__modal-list {
  position: absolute;
  top: 100%;
  z-index: 2;
  width: 136px;
  margin: 0;
  padding: 7px 14px;
  list-style: none;
  visibility: hidden;
}
.nav__modal-list li + li {
  margin-top: 2px;
}
.nav__modal-list a {
  display: block;
  padding: 5px 0;
  transition: 200ms opacity;
}
.nav__modal-list a:hover, .nav__modal-list a:focus {
  opacity: 0.8;
}